'use client'

import { UsuarioService } from "@/utils/axios"
import { useState } from "react"

const usuarioService = new UsuarioService()

export default function Mudarnome() {

    const [useId, setId] = useState<Number>()
    const [useName, setName] = useState<String>()

    function handleSubmit() {
        let data = {
            id: Number(useId),
            name: String(useName)
        }
        usuarioService.mudarNome(data)
            .then((response) => {
                console.log(response)
            }).catch((error) => {
                console.error(error)
            })
    }

    return (
        <form action="">
            <span>id</span>
            <input type="number" name="" id="" onChange={(e) => { setId(Number(e.target.value)) }} />
            <span>name</span>
            <input type="text" onChange={(e) => { setName(String(e.target.value)) }} />
            <button type="button" onClick={handleSubmit}>Enviar</button>
        </form>
    )
}